import React, {useRef, useState, useEffect} from 'react'
import useObserver from './useObserver'
import push1 from './medias/push1.jpg'
import push2 from './medias/push2.jpg'
import push3 from './medias/push3.jpg'

function PushItem({img}) {

    const [contentRef,
        visible] = useObserver({root: null, rootMargin: "0px", treshold: 0})

    return (
        <div ref={contentRef} className={`push-item hidden ${visible && "reveal"}`}>
            <div className="img">
                <img className="cover" src={img} alt="img"/>
            </div>
            <a href="#" className="link">View product</a>
        </div>
    )
}

export default function Push() {

    return (
        <div className="strate-push">
            <PushItem img={push1}/>
            <PushItem img={push2}/>
            <PushItem img={push3}/>
        </div>
    )

}
