import React, {useState, useRef, useEffect} from 'react'

export default function useObserver(options) {
    const contentRef = useRef(null)
    const [visible,
        setVisible] = useState(false)

    function callbackFunction(entries) {
        const [entry] = entries;
        setVisible(entry.isIntersecting)
    }

    useEffect(() => {
        const observer = new IntersectionObserver(callbackFunction, options)
        contentRef.current && observer.observe(contentRef.current)
        return () => {
            contentRef.current && observer.disconnect(contentRef.current)
        }
    }, [contentRef, options])

    return [contentRef, visible]
}