import React, {useRef, useState, useEffect} from 'react'
import useObserver from './useObserver'
import img1 from './medias/img1.jpg'

export default function Story({direction}) {

    const [contentRef1,
        visible] = useObserver({root: null, rootMargin: "0px", treshold: 0})

    const [contentRef2,
        visible2] = useObserver({root: null, rootMargin: "0px", treshold: 0})

    return (
        <div
            className="strate-story"
            style={{
            flexDirection: direction
        }}>
            <div ref={contentRef1} className={`img-story--3-4 hidden ${visible && "reveal"}`}>
                <img className="cover" src={img1} alt="img"/>
            </div>
            <div ref={contentRef2} className={`text-story hidden ${visible2 && "reveal"}`}>
                <h1 className="title">On the other hand</h1>
                <p className="paragraph">But I must explain to you how all
                    this mistaken idea of denouncing pleasure and praising pain was born and I will
                    give you a complete account of the system, and expound the actual teachings of
                    the great explorer</p>
                <div className="btn">Discover</div>
            </div>
        </div>

    )

}