import React, {useRef, useState, useEffect} from 'react'
import useObserver from './useObserver'
import imgStart from './medias/imgStart.jpg'
import nat from './medias/nat.jpg'
import pho from './medias/pho.jpg'

export default function Starter({isFirst, classText, padding}) {

    const [contentRef,
        visible] = useObserver({root: null, rootMargin: "0px", treshold: 0})
    

    function setPosition() {
        let response = "3rem"
        isFirst
            ? response = "5rem"
            : response = "3rem"
        return response
    }

    return (
        <div
            ref={contentRef}
            style={{
            padding: `${padding}`
        }}
            className={`starter`}>
            <img className={`cover ${visible && "reveal"}`} src={imgStart} alt="img"/>
            <div className="text-starter">
                <h1
                    style={{
                    fontSize: setPosition()
                }}
                    className={`title hidden ${visible && "reveal"} ${classText}`}>On the other hand</h1>
                <p className={`paragraph hidden ${visible && "reveal"}`}>But I must explain to you how all
                    this mistaken idea of denouncing pleasure and praising pain was born and I will
                    give you a complete account of the system, and expound the actual teachings of
                    the great explorer</p>
                <div className={`btn hidden ${visible && "reveal"}`}>Discover</div>
            </div>
        </div>
    )

}